
*, *:before, *:after {
  box-sizing: border-box;
}
nav ul, nav li {
  list-style: none;
  padding: 0;
  margin: 0;
}
.hiding{
  display:none !important;
}
nav a {
  display: block;
  text-decoration: none;
}
nav a:hover, nav a:visited {
  text-decoration: none;
}
.menu-link {
  padding-top: 12px;
  padding-bottom: 12px;
  padding-right: 5px;
  background: #093145;
  color: #fff;
  transition: background .2s, color .2s;
  position: relative;
  z-index: 1;

 
}

.Mega-Menu-li{
  padding-right:5px !important;
}
.menu-link[aria-haspopup="true"] {
  padding-right: 30px;
}
.menu-link[aria-haspopup="true"]:after {
  content: "";
  background-image: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/1397521/arrowRight.svg#light');
  color:#fff;
  background-size: 14px;
  width: 14px;
  height: 14px;
  right:15px;
  font-size: 12px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  margin-left: .255em;
  vertical-align: .255em;
}
// Header Link inside the Mega Menu
.mega-menu-header {
  font-size: 1.2em;
  text-transform: uppercase;
  font-weight: bold;
  color: #fff;
}
.mega-menu {
  background: #ffffff;
  z-index: 10;
  display:none;
}
.mega-menu--multiLevel {
  flex-direction: column;
}
/* Media 1111111 lagre than 951 */
@media all and (min-width: 951px) {
  .nav {
    margin-top: 50px;
    background: #ffffff;
 }
  .nav > nav {
    max-width: 900px;
    margin: 0 auto;
 }
  .Mega-Menu-li [aria-haspopup="true"] ~ ul {
    display: none;
 }
 .Mobile-Header{
   display:none ;
    width:0 !important;
 }
  .menu-bar {
    position: relative;
 }
  .Mega-Menu-li > [aria-haspopup="true"]:after {
    background-image: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/1397521/arrowBottom.svg#light');
    right:-6px;
 }

  .Mega-Menu-li > [aria-haspopup="true"]:focus ~ ul {
    display: flex;
    transform-origin: top;
    animation: dropdown .2s ease-out;
    max-width: 100vw;
    top: 120px;
    width:100vw;
 }
 .Mega-Menu-li > [aria-haspopup="true"] ~ ul:focus {
  display: flex;
  transform-origin: top;
  animation: dropdown .2s ease-out;
  max-width: 100vw;
  top: 120px;
  width:100vw;
}
 .mega-menu {
  max-width: 100vw;
  top: 120px;
  width:100vw;
 }
 .Mega-Menu-li > [aria-haspopup="true"]:hover ~ ul{
  display: flex;

  transform-origin: top;
  animation: dropdown .2s ease-out;
  max-width:150vw;
  top: 120px;
  position:fixed;
  width:100vw;
 }
  .Mega-Menu-li:focus-within > [aria-haspopup="true"] ~ ul {
    display: flex;

    transform-origin: top;
    animation: dropdown .2s ease-out;
    max-width:150vw;
    top: 120px;
    position:fixed;
    width:100vw;
 }
  .Mega-Menu-li > [aria-haspopup="true"]:focus, .Mega-Menu-li:focus-within > [aria-haspopup="true"], .Mega-Menu-li:hover > a {
    background: #093145;
    color: #ffffff;
 }
  .Mega-Menu-li > [aria-haspopup="true"]:focus:after, .Mega-Menu-li:focus-within > [aria-haspopup="true"]:after, .Mega-Menu-li:hover > a:after {
    background-image: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/1397521/arrowTop.svg#light');
 }
  .mega-menu {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
 }
  .mega-menu:hover {
    display: flex;
 }
  .mega-menu a:hover {
    background: white;
    color: black;
 }
 .mega-menu a{
  background: white;
  color: black
 }
 .mega-menu  li {
   width:150px !important;
   margin:10px;
 }
  
  .mega-menu--multiLevel > li > [aria-haspopup="true"] ~ ul {
    width:100%;
    margin:10px;
 }
  .mega-menu--multiLevel > li > [aria-haspopup="true"] ~ ul ul {
    width: 100%;
    left: 100%;
 }
  .mega-menu--multiLevel li:hover > [aria-haspopup="true"] ~ ul {
    display: block;
    transform-origin: left;
    animation: flyout .2s ease-out;
 }
  .mega-menu--multiLevel li:focus-within > [aria-haspopup="true"] ~ ul {
    display: block;
 }
  .mega-menu--multiLevel li:hover > [aria-haspopup="true"], .mega-menu--multiLevel li:hover > a, .mega-menu--multiLevel li:focus-within > [aria-haspopup="true"], .mega-menu--multiLevel li:focus-within > a {
    background: tint(#fff,85%);
    color: #fff;
 }
  .mega-menu--multiLevel [aria-haspopup="true"] ~ ul, .mega-menu--multiLevel [aria-haspopup="true"] {
    border-left: 1px solid #f0f0f0;
 }
  .mega-menu--multiLevel [aria-haspopup="true"] ~ ul:hover, .mega-menu--multiLevel [aria-haspopup="true"]:hover {
    display: block;
 }
  .mega-menu--multiLevel [aria-haspopup="true"] ~ ul {
    position: absolute;
    top: 0;
    height: 100%;
 }
  .mega-menu--flat > * {
    flex: 1;
 }
  .mobile-menu-trigger, .mobile-menu-back-item {
    display: none;
 }
}/* /Media 1111111 large than 951*/

                                          /* /Media 2222222 less than  950*/
                                          @media all and (max-width: 950px) {
                                            
                                            .mobile-menu-back-item {
                                              display: none;
                                          }
                                            
                                           .menu-list{
                                             display:none;
                                           } 

                                            .Mega-Menu-li > [aria-haspopup="true"] ~ ul  {
                                              display: flex;
                                           
                                            flex-direction: column;
                                            background: #093145;
                                            position: absolute;
                                            left: 100%;
                                            top: 50px;
                                            max-height: 100vh;
                                            width: 100%;
                                            height: 100vh;
                                            transition: left .3s;  
                                          }
                                          .mega-menu-header:after{
                                            content: "";
                                            background-image: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/1397521/arrowRight.svg#light');
                                            color:#fff;
                                            background-size: 14px;
                                            width: 14px;
                                            height: 14px;
                                            right:15px;
                                            font-size: 12px;
                                            position: absolute;
                                            top: 50%;
                                            transform: translateY(-50%);
                                            margin-left: .255em;
                                            vertical-align: .255em;
                                          }
                                          .Mobile-Header:before{
                                            content: "";
                                            background-image: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/1397521/arrowLeft.svg#light');
                                            color:#fff;
                                            background-size: 14px;
                                            width: 14px;
                                            height: 14px;
                                            left:-20px;
                                            
                                            font-size: 12px;
                                            position: absolute;
                                            top: 50%;
                                            transform: translateY(-50%);
                                            margin-left: .255em;
                                            vertical-align: .255em;
                                          }
                                          .Mobile-Header{ 
                                              border-bottom: 1px solid #a5a4a4;
                                              margin-left:10px;
                                              width:300px;
                                              left:10px;
                                          }
                                          .Mobile-Header:after{
                                            content:'';
                                            background-image: none;
                                            
                                          }
                                          .mega-menu-header:focus-within{
                                            display: block;
                                          }
                                         
                                          .mega-menu-header:focus-within +ul{
                                             display: block;
                                            flex-direction: column;
                                            background: #093145;
                                            position: absolute;
                                            top:10px;
                                            left:12px;
                                            max-height: 1500px;
                                            width: 100%;
                                            height: 100vh;
                                            z-index: 15;
                                            transition: left .3s; 

                                          }
                                            .Mega-Menu-li  > [aria-haspopup="true"] ~ ul > li > [aria-haspopup="true"] {
                                              font-size: 1.2em;
                                          }
                                            .Mega-Menu-li  > [aria-haspopup="true"] ~ ul > li > [aria-haspopup="true"] ~ ul a {
                                              padding-left: 40px;
                                          }
                                            .Mega-Menu-li  > [aria-haspopup="true"] ~ ul > li > [aria-haspopup="true"] ~ ul > li > [aria-haspopup="true"] ~ ul a {
                                              padding-left: 80px;
                                          }
                                            .Mega-Menu-li  > [aria-haspopup="true"] ~ ul [aria-haspopup="true"] {
                                              color: #2a2a2a;
                                          }
                                            .Mega-Menu-li  > [aria-haspopup="true"] ~ ul [aria-haspopup="true"]:after {
                                              content: "+";
                                              background: none;
                                              font-size: 1em;
                                              font-weight: normal;
                                              height: 20px;
                                              line-height: 1;
                                          }
                                            .Mega-Menu-li  > [aria-haspopup="true"] ~ ul [aria-haspopup="true"] ~ ul {
                                              max-height: 0px;
                                              transform-origin: top;
                                              transform: scaleY(0);
                                              transition: max-height .1s;
                                          }
                                            .mega-menu-content {
                                              padding: 20px 25px;
                                          }
                                            .mobile-menu-back-item {
                                              order: -1;
                                          }
                                            .mobile-menu-back-item a {
                                              background: tint(grey,70%);
                                              color: #fff;
                                              max-height: calc(1.4em + 40px);
                                              margin-top: calc(0px - (1.4em + 40px));
                                              pointer-events: none;
                                          }
                                            .mobile-menu-back-item a:before {
                                              content: "";
                                              width: 14px;
                                              height: 12px;
                                              background-image: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/1397521/arrowLeft.svg#light');
                                              background-size: 14px;
                                              margin-right: 10px;
                                              display: inline-block;
                                          }

                                            .menu-bar:hover, .menu-bar:focus-within {
                                              left: 0;
                                              top:50px;
                                              z-index: 5;
                                              position: absolute;
                                              width: 100vw;
                                              max-width: 600px;
                                              height: 100vh;
                                          }
                                            .Mega-Menu-li  > [aria-haspopup="true"]:focus ~ ul {
                                              left: 0;
                                              z-index: 5;
                                              position: static;
                                          }
                                            
                                            .Mega-Menu-li  > [aria-haspopup="true"] ~ ul:hover, .Mega-Menu-li  > [aria-haspopup="true"] ~ ul:focus-within {
                                            left:0;
                                            
                                          }
                                            .Mega-Menu-li  > [aria-haspopup="true"] ~ ul [aria-haspopup="true"]:focus ~ ul {
                                              animation: dropdown .3s forwards;    
                                              left: 0;
                                              z-index: 2;
                                              position: absolute;
                                              margin: auto;
                                              max-width: 700px;
                                              max-height: 100vh;
                                              width: 100vw;
                                          }
                                        
                                            .Mega-Menu-li  > [aria-haspopup="true"] ~ ul li:focus-within > [aria-haspopup="true"] ~ ul {
                                              max-height: 500px;
                                              transform: scaleY(1);
                                          }

                                          }
@media all and (max-width: 950px) and (hover: none) {
  .mobile-menu-trigger:hover ~ ul {
   position: absolute;
   left:0;
   top:80px;
   display: block;
   max-height: 500px;
   animation: dropdown .3s forwards;
 }
  .Mega-Menu-li  > [aria-haspopup="true"]:hover ~ ul {
    position: absolute;
    top:80px;
    left:0;
    padding-left:15px;
 }
  .Mega-Menu-li  > [aria-haspopup="true"] ~ ul:hover {
    left: 0;
    padding-left:15px;
 }

  .Mega-Menu-li  > [aria-haspopup="true"] ~ ul [aria-haspopup="true"]:hover ~ ul {
    max-height: 500px;
    animation: dropdown .3s forwards;
 }
  .Mega-Menu-li  > [aria-haspopup="true"] ~ ul [aria-haspopup="true"] ~ ul:hover {
    max-height: 500px;
    transform: scaleY(1);
 }
 
}
@keyframes dropdown {
  0% {
    opacity: 0;
    transform: scaleY(0);
 }
  50% {
    opacity: 1;
 }
  100% {
    transform: scaleY(1);
 }
}
@keyframes flyout {
  0% {
    opacity: 0;
    transform: scaleX(0);
 }
  100% {
    opacity: 1;
    transform: scaleX(1);
 }
}
